import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import { Section } from '@components/global';
import Img from 'gatsby-image';
import { StyledContainer, StyledArt } from './StyledCase';
const Case = () => (
  <StaticQuery
    query={graphql`
      query {
        art_story: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "tell_story" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1200) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="case" accent>
        <StyledContainer>
          <div>
            <h1>合作案</h1>
          </div>
          <StyledArt>
            <Img fluid={data.art_story.childImageSharp.fluid} />
          </StyledArt>
        </StyledContainer>
      </Section>
    )}
  />
);

export default Case;
